.loader {

    margin: 5rem auto;
    text-align: center;

    svg {
      height: 3.5rem;
      width: 3.5rem;
      fill: #F59A83;
      transform-origin: 44% 50%;
      animation: rotate 1.5s infinite linear;
    }
}

@keyframes rotate {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}